@font-face {
    font-family: 'geomanist';
    src: url('../fonts/geomanist-thin-webfont/geomanist-thin-webfont.eot');
    src: url('../fonts/geomanist-thin-webfont/geomanist-thin-webfont.eot?#iefix') format('embedded-opentype'),
         url('../fonts/geomanist-thin-webfont/geomanist-thin-webfont.woff2') format('woff2'),
         url('../fonts/geomanist-thin-webfont/geomanist-thin-webfont.woff') format('woff'),
         url('../fonts/geomanist-thin-webfont/geomanist-thin-webfont.ttf') format('truetype'),
         url('../fonts/geomanist-thin-webfont/geomanist-thin-webfont.svg#geomanistthin') format('svg');
    font-weight: 100;
    font-style: normal;
}

