
.splash{
   @include split-rows();
   @include left-middle();
   height: 100vh;

    h1 {
        margin-top: auto;
        flex: 0 0 auto;
        @extend %title;
       
    }

    h2 {
        flex: 0 0 auto;
              @extend %subhead-lg;
    }

    h3 {
        flex: 0 0 auto;
              @extend %display-text-lg
    }

    p {
        flex: 0 0 auto;
        margin-bottom: auto;
              @extend %body-text-lg;

    }
    .link{
    	position: absolute;
      bottom: 20rem;
      left: 50%;

    }
    & :nth-last-child(2){
  flex: .25 0 auto;
    }
}